function AccountButton(props: EnhancerProps): JSX.Element {
  return (
    <Popover
      content={
        <Pane paddingX={majorScale(4)} paddingY={majorScale(2)} width={320}>
          <Row middle="xs">
            <Col xs>
              <Heading size={400} fontWeight={`bolder`}>
                {addressShortener("0xf1D4D16aAc4E116120312BA0D5670cF0662650AD")}
              </Heading>
            </Col>
            <Col>
              <Row end="xs">
                <CopyButton marginRight={majorScale(1)} />
                <ExploreButton marginRight={majorScale(1)} />
                <DisconnectButton marginRight={majorScale(2)} />
              </Row>
            </Col>
          </Row>
          <Row middle="xs" center="xs">
            <Col xs>
              <Heading size={800} marginY={majorScale(3)}>
                0.754 grETH
              </Heading>
            </Col>
          </Row>
          <Row>
            <Col xs>
              <BuyTokensButton width="100%" />
            </Col>
          </Row>
          <Divider marginY={16} />
          <TransactionsButton marginBottom={2} width="100%" />
          <LanguageButton marginBottom={2} width="100%" />
          <ThemeButton width="100%" />
        </Pane>
      }
    >
      <Button
        {...props}
        borderRadius={20}
        iconBefore={<UserIcon />}
        iconAfter={<ChevronDownIcon />}
        fontWeight={`bolder`}
      >
        {addressShortener("0xf1D4D16aAc4E116120312BA0D5670cF0662650AD")}
      </Button>
    </Popover>
  );
}

