function PortraitNavigation(): JSX.Element {
  return (
    <Row middle="xs">
      <Col xs>
        <Heading size={600}>Nav</Heading>
      </Col>

      <Col>
        <Row end="xs">
          <Col xs>
            <SearchButton marginRight={10} />
            <MoreButton marginRight={10} />
            <WalletButton />
          </Col>
        </Row>
      </Col>
    </Row>
  );
}

function LandscapeNavigation(): JSX.Element {
  return (
    <Row middle="xs">
      <Col xs>
        <Heading size={600}>PortraitNavigation</Heading>
      </Col>

      <Col xs>
        <Row center="xs">
          <Col xs>
            <SearchBar width={`100%`} />
          </Col>
        </Row>
      </Col>

      <Col xs>
        <Row end="xs">
          <Col xs>
            <MoreButton marginRight={10} />
            <WalletButton />
          </Col>
        </Row>
      </Col>
    </Row>
  );
}

export function Navigation(): JSX.Element {
  const isPortrait = useMediaQuery({ query: "(orientation: portrait)" });

  return (
    <Pane paddingX={16} paddingY={10}>
      {isPortrait ? <PortraitNavigation /> : <LandscapeNavigation />}
    </Pane>
  );
}
