import { useEffect } from "react";
import { useState } from "react";
import ReactApexChart from "react-apexcharts";

export function Chart(): JSX.Element {
  const [data, setData] = useState([...new Array(5)]);

  useEffect(
    () => setData((d) => d.map((r) => Math.random() * 5 + 10)),
    [setData]
  );

  return (
    <ReactApexChart
      series={[
        {
          name: "Series 1",
          data,
        },
      ]}
      type="area"
      options={{
        chart: {
          animations: { enabled: false },
          selection: { enabled: false },
          toolbar: { show: false },
          zoom: { enabled: false },
        },
        legend: { show: false },
        stroke: { curve: "smooth", colors: ["transparent"] },
        dataLabels: { enabled: false },
        grid: {
          show: false,
          padding: {
            bottom: 0,
            left: -10,
            right: 0,
          },
        },

        xaxis: { labels: { show: false } },
        yaxis: { labels: { show: false } },
        fill: {
          type: "gradient",
          gradient: {
            shadeIntensity: 1,
            opacityFrom: 0.7,
            opacityTo: 0.4,
            stops: [0, 100],
          },
        },
        tooltip: {
          enabled: false,
        },

        states: {
          hover: {
            filter: { type: "none" },
          },
        },
      }}
    />
  );
}
