import { Col, Row } from "react-flexbox-grid";
import {
  Avatar,
  Card,
  Heading,
  IconButton,
  majorScale,
  minorScale,
  Pane,
  StarEmptyIcon,
  StarIcon,
} from "evergreen-ui";
import { useCallback } from "react";
import { Chart } from "./Chart";
import { useState } from "react";

interface ProjectCardProps {
  num: number;
}

export function ProjectCard(props: ProjectCardProps): JSX.Element {
  const [favorite, setFavorite] = useState(false);

  const TokenName = useCallback(
    () => (
      <Heading size={600} fontWeight={`bolder`} opacity={0.8}>
        Alan Turin Token
      </Heading>
    ),
    []
  );

  const WatchTokenButton = useCallback(
    () => (
      <IconButton
        appearance="minimal"
        size="large"
        onClick={() => setFavorite(!favorite)}
        icon={
          favorite ? (
            <StarIcon color="warning" />
          ) : (
            <StarEmptyIcon color="warning" />
          )
        }
      />
    ),
    [setFavorite, favorite]
  );

  const TokenLogo = useCallback(
    () => (
      <Avatar
        src={`https://picsum.photos/seed/${props.num}/40/40`}
        name="Alan Turing"
        size={40}
      />
    ),
    [props]
  );

  const TokenSymbol = useCallback(
    () => (
      <Heading size={600} marginLeft={minorScale(3)} opacity={0.4}>
        ATT
      </Heading>
    ),
    []
  );

  const TokenAmount = useCallback(
    () => (
      <Heading size={600} fontWeight={`bolder`} opacity={0.8}>
        500,00 ATT
      </Heading>
    ),
    []
  );

  return (
    <Card backgroundColor="white" cursor="pointer" hoverElevation={4}>
      <Pane padding={majorScale(5)}>
        <Row middle="xs" between="xs">
          <Col>
            <TokenName />
          </Col>
          <Col>
            <WatchTokenButton />
          </Col>
        </Row>
        <Pane marginBottom={minorScale(2)} />
        <Row middle="xs" between="xs">
          <Col>
            <TokenLogo />
          </Col>

          <Col>
            <TokenSymbol />
          </Col>

          <Col xs />
          <Col>
            <TokenAmount />
          </Col>
        </Row>
      </Pane>

      <Chart />
    </Card>
  );
}
